.swagger-ui .body-param-options {
  display: none;
}
.swagger-ui .info {
  display: none;
}
.swagger-ui .opblock {
  box-shadow: none;
  border-radius: 3px;
  margin: 0 0 9px;
}
.swagger-ui .opblock .opblock-section-header label {
  display: none;
}
.swagger-ui .opblock .opblock-summary-method {
  font-size: 13px;
  border-radius: 2px;
  font-weight: normal;
}
.swagger-ui .opblock-tag {
  font-size: 18px;
  border-bottom: none;
  margin-bottom: 0;
  padding-left: 2px;
}
.swagger-ui .opblock-description-wrapper p + p {
  margin-top: 0.5em;
}
.swagger-ui .scheme-container {
  display: none;
}